* {
  box-sizing: border-box;
}
.no-event {
  opacity: 0.7;
  cursor: not-allowed;
  pointer-events: none;
  user-select: none;
}
.has-error {
  border: 1px solid var(--riverbed-error-color);
}
.form-control:focus,
.form-control:active {
  outline: none !important;
  box-shadow: none !important;
}
.form-control.is-invalid {
  color: var(--riverbed-error-color);
  border-color: var(--riverbed-error-color) !important;
  stroke: var(--riverbed-error-color) !important;
}
.ad-form {
  margin: 1rem auto;

  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.18);
  border-radius: 8px;

  text-align: left;
  max-width: 80%;
}
.ad-form-header {
  text-align: left;
  display: block;
  opacity: 0.8;
  padding: 0.5rem 1rem;
  color: #fff;
  background-color: var(--riverbed-orange);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  color: #fff;
}
.ad-form-header-title {
  font-size: 1.5rem;
  font-weight: 900;
}
.ad-form-description {
  font-size: 0.85rem;
  font-style: italic;
}
.ad-form-controls-container {
  display: grid;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  grid-gap: 1rem;
  padding: 0.5rem 1rem;
}
.ad-form fieldset {
  grid-column: span 12;
  margin: 1rem 0;
  border: 1px solid;
  padding: 0.75rem 0.75rem 1rem 0.75rem;
  padding-top: 0px;
  border-radius: 4px;
}
.ad-form legend {
  background-color: #fff;
  display: inline-block;
  width: auto;
  padding: 0 0.5rem;
  opacity: 1;
  margin-left: 3px;
  margin-top: -1rem;
}

.ad-form-control-holder {
  overflow: hidden;
}
.ad-form-control-holder.sm {
  grid-column: span 4;
}
.ad-form-control-holder.md {
  grid-column: span 6;
}
.ad-form-control-holder.lg {
  grid-column: span 12;
}
.ad-form-control.inline-help {
  display: flex;
  flex-wrap: nowrap;
  align-items: baseline;
}

.ad-form-control-help {
  cursor: help;
  margin-left: 0.25rem;
  transform: translateY(-2px);
  display: inline-block;
}
.ad-form-control-label {
  font-weight: 550;
}

.ad-form-control-error {
  font-size: 0.8rem;
  text-align: right;
  padding: 0.25rem;
  color: red;
}

.ad-form-footer {
  grid-column: span 12;
  display: grid;
  grid-template-columns: auto auto;
  padding: 0.5rem 1rem;
  justify-content: end;
}
.ad-form-footer .btn {
  margin: 0 0.5rem;
}
.ad-form-footer .btn:first-of-type {
  margin-left: 0px;
}
.ad-form-footer .btn:last-of-type {
  margin-right: 0px;
}

.ad-form-select {
  position: relative;
  width: 100%;
  display: block;
  user-select: none;
}
.ad-form-select-val {
  padding: 0.4rem;
  cursor: pointer;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
}
.ad-form-select-val.active {
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}
.ad-form-select-val::after {
  position: absolute;
  content: "";
  top: 0.9rem;
  right: 0.6rem;
  width: 0;
  height: 0;
  border: 0.4rem solid transparent;
  border-color: #ced4da transparent transparent transparent;
}
.ad-form-select-val.active::after {
  border-color: transparent transparent #ced4da transparent;
  top: 0.45rem;
}
.ad-form-select-options {
  position: fixed;
  display: none;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  padding: 0.5rem;
  z-index: 1100;
  max-height: 10rem;
  overflow: hidden;
  overflow-y: auto;
  border-top: none;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  background-color: #fff;
}
.ad-form-select-options.show {
  display: block;
}

.ad-form-select-option {
  padding: 0.25rem;
  cursor: pointer;
}
.ad-form-select-option.selected::before {
  content: "";
  height: 0.25rem;
  width: 0.5rem;
  background-color: var(--riverbed-orange);
  position: absolute;
  transform: rotateZ(224deg);
  right: 1.5rem;
  margin-top: 0.75rem;
}
.ad-form-select-option.selected::after {
  content: "";
  height: 1rem;
  width: 0.25rem;
  background-color: var(--riverbed-orange);
  position: absolute;
  transform: rotateZ(44deg);
  right: 1.15rem;
  margin-top: 0.3rem;
}

.adform-checkbox-container,
.adform-radio-container,
.ad-form-toggle-container {
  line-height: 1.5;
  padding: 0.25rem 0;
  margin-bottom: 0;
  display: flex;
  align-items: stretch;
  gap: 0.5rem;
}
span.checkbox,
span.radio {
  display: inline-block;
  height: 1.2rem;
  width: 1.2rem;
  border: 2px solid var(--riverbed-dark-dirty);
  cursor: pointer;
  border-radius: 0.2rem;
  user-select: none;
  margin-right: 0.2rem;
}
span.checkbox.checked {
  border-color: var(--riverbed-orange);
  background-color: var(--riverbed-orange);
}
span.checkbox.checked::before {
  content: "";
  height: 0.5rem;
  width: 0.2rem;
  background-color: #fff;
  position: absolute;
  margin-top: 0.3rem;
  margin-left: 0.05rem;
  transform: rotateZ(320deg);
}
span.checkbox.checked::after {
  content: "";
  height: 0.9rem;
  width: 0.2rem;
  background-color: #fff;
  position: absolute;
  margin-left: 0.5rem;
  margin-top: 0.1rem;
  transform: rotateZ(50deg);
}
.adform-radio-container {
  flex-wrap: wrap;
}
.adform-radio-container .adform-single-radio-wrapper {
  display: flex;
}
span.radio {
  border-radius: 100%;
}
span.radio.checked {
  border-color: var(--riverbed-orange);
}
span.radio.checked::before {
  display: inline-block;
  position: absolute;
  content: "";
  width: 0.6rem;
  height: 0.6rem;
  background-color: var(--riverbed-orange);
  border-radius: 100%;
  margin: 0;
  transform: translate(0.17rem, 0.17rem);
}

span.adform-toggle {
  display: inline-block;
  height: 1.3rem;
  width: 2rem;
  background-color: var(--riverbed-common-color-for-both-1);
  cursor: pointer;
  border-radius: 0.2rem;
  user-select: none;
  margin-right: 0.2rem;
  border-radius: 0.6rem;
  transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
}
span.adform-toggle.active {
  background-color: var(--riverbed-orange);
}
span.adform-toggle::before {
  content: "";
  height: 1rem;
  width: 1rem;
  display: inline-block;
  background-color: var(--riverbed-dark-dirty);
  border-radius: 100%;
  transform: translate(0.1rem, 0.09rem);
  transition: transform 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out;
}
span.adform-toggle.active::before {
  background-color: #fff;
  transform: translate(0.9rem, 0.06rem);
}

.ad-form-control-multiple {
  display: grid;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  grid-gap: 10px;
}

.adform-submit-button svg path,
.ad-form-reset-button svg path {
  stroke: currentColor;
}

@media (max-width: 764px) {
  .ad-form-control-holder.sm {
    grid-column: span 6;
  }
  .ad-form-control-holder.md,
  .ad-form-control-holder.lg {
    grid-column: span 12;
  }
}

@media (max-width: 400px) {
  .ad-form-control-holder.sm,
  .ad-form-control-holder.md,
  .ad-form-control-holder.lg {
    grid-column: span 12;
  }
}
