@import url("bootstrap/dist/css/bootstrap.css");
.App {
  font-family: sans-serif;
  text-align: center;
}
:root {
  --riverbed-orange: #ff671b;
  --riverbed-light: #fefefe;
  --riverbed-light-dirty: #eee;
  --riverbed-dark: #555;
  --riverbed-dark-dirty: #666;
  --riverbed-border-dark-light: #ccc;
  --riverbed-error-color: #f40808;
  --riverbed-success-font: #31ac4c;
  --riverbed-fail-font: #721c24;
  --riverbed-information-color: #39a4b6;

  --riverbed-common-color-for-both-1: #d3d3d3;

  /*
* background-colors 
*/
  --riverbed-header-dark-bck: #212f3c;
  --riverbed-header-light-bck: #fff;
  --riverbed-sidemenu-dark-bck: #212f3c;
  --riverbed-sidemenu-light-bck: #fff;
  --riverbed-sidemenu-submenu-dark-bck: #314354;
  --riverbed-sidemenu-submenu-light-bck: #f1f1f1;

  --riverbed-body-dark-bck: #364a5f;
  --riverbed-body-light-bck: #fafafa;
  --riverbed-item-on-body-dark-bck: #212f3c;
  --riverbed-item-on-body-light-bck: #fff;

  --riverbed-table-header-light: #dfdfdf;
  --riverbed-table-header-dark: #4d555a;

  --riverbed-header-zindex: 150;
  --riverbed-menu-zindex: 120;
  --riverbed-custom-alert-zindex: 200;
}

.btn-riverbed {
  background-color: var(--riverbed-orange);
  color: #fff;
  border-color: var(--riverbed-orange);
}
.btn-riverbed:hover {
  color: #fff;
}
